.visuality-hidden {
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  opacity: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.scroll-block {
  overflow: hidden;
}

.scroll-up {
  z-index: 1200;
  width: 50px;
  height: 50px;
  color: #f87719;
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  border-radius: 50px;
  transition: all .2s;
  position: fixed;
  bottom: 50px;
  right: 50px;
  transform: translateY(25px);
  box-shadow: 0 0 0 4px #b7b7b7;
}

.scroll-up:before {
  width: 100%;
  height: 100%;
  content: "↑";
  justify-content: center;
  align-items: center;
  font-size: 30px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.scroll-up:hover {
  color: orange;
}

.scroll-up-active {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.scroll-up-path {
  box-sizing: border-box;
  fill: none;
  stroke: #f87719;
  stroke-width: 4px;
  transition: all .2s;
}

